import React, { FC } from "react";

import { StyledWorkspace } from "./Workspace.styles";
import { WorkspaceProps } from "./Workspace.types";

const Workspace: FC<WorkspaceProps> = ({
	children,
	dataQA,
	className,
	contentSize = "withSidebar",
	zIndex }) => (
	<StyledWorkspace
		as={contentSize === "fullScreen" ? "div" : "main"}
		data-qa={dataQA}
		className={className}
		contentSize={contentSize}
		zIndex={zIndex}>
		{children}
	</StyledWorkspace>
);

Workspace.displayName = "Workspace";
export default Workspace;
